.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}
p{
  color:white;
}
.p-browser {
  height: 20px;
  background-color: #6B6DFF;
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.p-img {
  width: 50%;
  border-radius: 50%;
  height: 150px;
}



@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 20vh;
  }
}
